.post__body img {
    border-radius: 20px;
    width: 100%;
}
.post {
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;
}
.post__footer {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}
.post__verified {
    font-size: 14px !important;
    color: var(--twitter-color);
}
.post__headerSpecial {
    font-weight: 600;
    font-size: 12px;
    color: gray;
}
.post__header--text h3 {
    font-size: 15px;
    margin-bottom: 5px;
}
.post__header--description {
    margin-bottom: 10px;
    font-size: 15px;
}
.post__body{
    flex: 1;
    padding: 10px;
}
.post__avatar {
    padding: 20px;
}
.post__img {
    max-width: 600px;
}
.post__footer .MuiSvgIcon-root {
    cursor: pointer;
}
.post__footer .MuiSvgIcon-root:nth-child(1) {
    cursor: no-drop;
}
.post__footer .MuiSvgIcon-root:nth-child(2) {
    cursor: no-drop;
}
.favorite:active{
    color: red ;
}