.sidebar__option {
    display: flex;
    align-items: center;
    cursor: no-drop;
    padding: 20px;
    transition: all 100ms ease-out;
}
.sidebar__option .MuiSvgIcon-root {
    margin-right: 20px;
    cursor: pointer;
}
.sidebar__option h2 {
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
}
.sidebar__option:hover{
    background-color: #e8f5fe;
    border-radius: 30px;
    color: var(--twitter-color)

}
.sidebar__option--active {
    color: var(--twitter-color);
}